.container{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: gray;
}

.calculator{
  padding: 20px;
  border-radius: 10px;
  background-color: white;
}

form input {
  border: none;
  outline : 0;
  width: 60px;
  height: 60px;
  font-size: 16px;
  background-color: rgb(95, 95, 156);
  margin: 2px;
  border-radius:10px ;
  color: white;
  font-weight: bolder;
  cursor: pointer;
}

form input[type="button"]:hover{
  background-color: rgb(120, 86, 151);
  font-size: 20px;
}

form .display{
  display: flex;
  justify-content: flex-end;
  margin: 5px 0px 15px 0px;


}

form .display input{
  text-align: right;
  flex: 1;
  font-size: 40px;
  pad: 5px 10px;
  background-color: rgb(134, 116, 151);

}

.equal{
width: 122px;
}